<script>
import PriceBookDataService from "../../../services/DataService";
import DynamicForm from "../../../components/Common/DynamicForm.vue";
export default {
    components: {
        DynamicForm,
    },
    data() {       
        return {
            isLoading:false,
            value: "",
            LeadData: null,
            LeadOwner: null,
            GroupBy: [],
            Channel: null,
            IndustryType: null,
            Status: null,
            options: null,
            selected: false,
            selectoptions: [],
            isPriceInfo: false,
            discountType: 0,
            onEditPriceInfo: false,
            priceInformation: [],
            submited: false,
            vSelectValidate: false,
            pricebookid: this.$route.params.id,           
            DependentOption: [{
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                minDate: false
            }],
            userId:null,
            companyId:null,
            dateformat:null
        }
    },
    created: function () {
        this.userId= this.GetUserInfo.ID;
        this.companyId= this.GetUserInfo.CompanyId;
        this.dateformat= this.GetUserInfo.DateFormat;
        this.FetchData();
    },
    methods: {
        onSearch: function onSearch(search, objItem) {
            if(search)     {
            this.searchSelectData(search, this, objItem);
            }
        },
        searchSelectData: function (search, vm, objItem) {
            this.ShowSearchData(search, vm, objItem,objItem.value.value);
        },
        ShowSearchData(search, vm, objItem,selctedValue) {
            if (typeof (selctedValue) == 'undefined') {
                    selctedValue = 0;
                }
            var url = "id=&moduleName="+this.$route.params.moduleName+"&type="+objItem.field_code;           
            PriceBookDataService.getDDLData(url + "&search=" + escape(search))
                .then(response => {
                    var json = [];
                    if(response.data==''){
                       
                    }
                    if (response.data != '') {
                        json = response.data;
                    }                    
                         objItem.select_options = json.DATA;
                        return objItem.select_options;
                    
                });
        },
        setSelectedValue(item, val) {
                var vm = this;
                try {
                    if (Array.isArray(val)) {
                        item.value = (typeof val[0] === 'object' && val[0] !== null) ? val[0].value : val[0];
                    }
                    else {
                        item.value = val;
                    }
                }
                catch (ex) {
                }
            },
        DependentDateObject: function (item, date) {
            if (item.name == 'effective_start_date') {
                var checkDate = new Date();
                if ((typeof date !== 'undefined') && date != '') {
                    checkDate = new Date(Date.parse(date));
                }
                else {
                    checkDate =item.value;
                }
                this.DependentOption[0] = {
                    useCurrent: false,
                    showClear: true,
                    showClose: true,
                    allowInputToggle: true,
                    minDate: checkDate
                }
                $.each(this.GroupBy, function (filedIndex, fieldItem) {
                    $.each(fieldItem.Data, function (index, element) {
                        if (element.name == "effective_end_date") {
                            element.value = '';
                        }
                    });
                });
            }
        },
        discountFix: function (index, item) {
           var vm = this;
            if (item.indexOf('.') >= 0) {
                var parts = item.split('.');
                if (parts.length > 1) {
                    if (parts[1] != '') {
                        if (parts[1].length > 1) {
                            vm.priceInformation[index].discount = parseFloat(item).toFixed(2);
                        }
                        else {
                            vm.priceInformation[index].discount = parseFloat(item).toFixed(1);
                        }
                    }
                }
            }
        },
        priceDetails: function () {
            var dataObject = {
                rangeFrom: '',
                rangeTo: '',
                discount: ''
            };
            this.priceInformation.push(dataObject);
        },
        addNewForPriceDetails: function () {
            if (this.priceInformation[this.priceInformation.length - 1].rangeFrom != '' && this.priceInformation[this.priceInformation.length - 1].rangeTo != '') {
                var dataObject = {
                    rangeFrom: '',
                    rangeTo: '',
                    discount: ''
                };
                this.priceInformation.push(dataObject);
            } else {
                this.ShowAlert(this.$t("lastOneRowRequired"),"warning", true, this.$t("Alert"));
            }
        },
        deletePriceDetailRow: function (index) {
            var thisvar = this;
            if (thisvar.priceInformation.length == 1) {
                this.ShowAlert(this.$t("Atleastonerowismandatorytosave"),"warning", true, this.$t("Alert"));
                return false;
            }
            thisvar.confirmR(this.$t("AssetGroup_ConfirmDelete"), true, this.$t("Delete",['Price Details']), function () {
                thisvar.priceInformation.splice(index, 1);
                if (thisvar.priceInformation.length <= 0) {
                    thisvar.addNewForPriceDetails();
                }
            });
        },
        checkForFromRange(value) {
            if (parseInt(value.rangeFrom) >= parseInt(value.rangeTo)) {
                this.ShowAlert(this.$t("ToRangeValueShouldBeGreater"),"warning",true, this.$t("Alert"));
                value.rangeTo = '';
            }
        },
        checkForPreviousRange(value) {
            if (parseInt(value.rangeFrom) >= parseInt(value.rangeTo)) {
                value.rangeTo = '';
            }
            $.each(this.priceInformation, function (key, item) {
                if (parseInt(value.rangeFrom) <= parseInt(item.rangeTo)) {
                    this.ShowAlert(this.$t("rangeShouldBeGreaterThanPreviousOne"),"warning",true, this.$t("Alert"));
                    value.rangeFrom = '';
                    value.rangeTo = '';
                    value.discount = '';
                }
            });
        },
        changevalue: function (value) {
            
            if (parseInt(value.rangeFrom) >= parseInt(value.rangeTo)) {
                value.rangeTo = '';
            }
        },
      
        onchangeForNormalDDL(item) {
            try {
                if (item.name == 'price_book_model') {
                    if (item.value != '' && item.value !== undefined && item.value != "0") {
                        if (!this.onEditPriceInfo) {
                            this.priceInformation = [];
                            this.priceDetails();
                        }
                        else {
                            this.onEditPriceInfo = false;
                        }
                        this.isPriceInfo = true;
                        this.discountType = (item.value == null || item.value == "") ? 0 : item.value;
                    } else {
                        this.priceInformation = [];
                        this.isPriceInfo = false;
                    }
                }
            }
            catch (ex) {
            }
        },
        formatDate(value) {
            if (value) {
                return moment(String(value), "YYYY-MM-DD").format(String(Globalize.culture().calendar.patterns.d).toUpperCase())
            }
        },
        GetLocalizedValue: function (fieldName, displayName) {
            if (this.isBlank(displayName)) {
                var chk = this.$t(String(fieldName).toUpperCase());
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk = this.$t(String(fieldName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return fieldName;
            }
            else {
                var chk = this.$t(String(displayName).toUpperCase());
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk = this.$t(String(displayName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return displayName;
            }
        },
        GetFieldPlaceHolder: function (str, displayName, isfor) {
            var vm=this;
            var name = vm.GetLocalizedValue(str, displayName);
            if (isfor == "select") {
                name = vm.$t('SelectField')+ name;
            }
            else {
                name = vm.$t('EnterField')+ name;
            }
            return name;
        },
        isBlank: function (str) {
            return (!str || /^\s*$/.test(str));
        },
        SubmitData(values) {
            var vm = this;
            // this.LeadData.forEach((item) => {
            //     if (item.astype != '' && item.form_field_visibility == 'YES') {
            //         if (item.dt_code == "select" && item.value) {
            //             try {
            //                 values[item.name] = String((typeof item.value === 'object' && item.value !== null) ? item.value.value : item.value);
            //                 vm.$refs.form.setFieldTouched(item.name, false);
            //             } catch (ex) { console.log('error while get dropdown parse data') }
            //         }
            //         if (item.dt_code == 'date' && item.value) {
            //             values[item.name] =vm.$options.filters.formatDate(item.value);
            //             vm.$refs.form.setFieldTouched(item.name, false);
            //         } else {
            //             vm.$refs.form.setFieldTouched(item.name, true);
            //         }
            //     }
            // })
            // vm.priceInformation.forEach((item,index) => {
            // vm.$refs.form.setFieldTouched('rangeFrom'+index, true);
            // vm.$refs.form.setFieldTouched('rangeTo'+index, true);
            // vm.$refs.form.setFieldTouched('discount'+index, true);
            // })
            vm.$refs.form.validate().then((result) => {               
            // vm.priceInformation.forEach((item,index) => {
            //     if(values['rangeFrom'+index]){
            //         delete result.errors['rangeFrom'+index];
            //         result.results['rangeFrom'+index].valid=true;
            //         result.results['rangeFrom'+index].errors=[];
            //         result.valid= Object.keys(result.errors).length === 0?true:result.valid;
            // }
            // if(values['rangeTo'+index]){
            //         delete result.errors['rangeTo'+index];
            //         result.results['rangeTo'+index].valid=true;
            //         result.results['rangeTo'+index].errors=[];
            //         result.valid= Object.keys(result.errors).length === 0?true:result.valid;
            // }
            // if(values['discount'+index]){
            //         delete result.errors['discount'+index];
            //         result.results['discount'+index].valid=true;
            //         result.results['discount'+index].errors=[];
            //         result.valid= Object.keys(result.errors).length === 0?true:result.valid;
            // }                        
            //         })
                if (result) {                   
                    vm.isLoading=true;            
                    vm.submited = true;                     
                    var fullJson = vm.LeadData;
                    var data = [];
                    var validation = [];
                    $.each(fullJson, function (key, item) {
                        var validationObject = {
                            field_name: item.name,
                            required: item.is_required,
                            length: item.length,
                            type: item.validation_type
                        };
                        var fieldValue = (item.name == "price_book_id" ? vm.pricebookid : ((typeof item.value === "undefined") ? '' : item.value));
                        fieldValue = typeof fieldValue === "undefined" ? '' : fieldValue;
                        if (item.dt_code == "date") {
                            fieldValue = item.value;
                        }
                        if (item.dt_code == "checkbox") {
                            var filtered = item.select_value.filter(function (el) {
                                return (el != null && el != "");
                            });
                            fieldValue = filtered.join(',');
                        }
                        if (item.dt_code == "select") {
                            try {
                                fieldValue = (typeof fieldValue === 'object' && fieldValue !== null) ? fieldValue.value : fieldValue
                            } catch (ex) { }
                        }
                        if (item.name == 'created_by') {
                            fieldValue = vm.userId;
                        }
                        if (item.name == 'modified_by') {
                            fieldValue = vm.userId;
                        }
                        if (item.name == 'company_id') {
                            fieldValue = vm.companyId;
                        }
                        if (item.name == 'price_book_id') {
                            fieldValue = vm.pricebookid;
                        }
                        var dataObject = {
                            field_name: item.name,
                            field_value: fieldValue,
                            custom_field_id: item.custom_field_id
                        };
                        data.push(dataObject);
                        validation.push(validationObject);
                    });
                    var postJSON = {
                        data: data,
                        priceInformation: vm.priceInformation,
                        validation: validation
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    PriceBookDataService.PostPriceBook(objectPostString).then(response => {  
                        vm.isLoading=false;                               
                        if (response.data[0].Code == "1") {
                            if (vm.pricebookid > 0) {                        
                                vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('PriceBook')]), "success", true, vm.$t("Alert")); 
                            }
                            else {
                                vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('PriceBook')]), "success", true, vm.$t("Alert")); 
                            }
                            vm.$router.push({ name: 'CRMPriceBook' });
                        }
                        else {
                            vm.ShowAlert(vm.$t('AddedError', [vm.$t('PriceBook')]), "failure", true, vm.$t("Alert"));                            
                        }                               
                        vm.submited = false;                        
                    });
                }
            });
        },
        MakeArray: function (value, type) {
            var array = [];
            var arr = String(value).split(',');
            if (type == "radio" || type == "checkbox") {
                if (arr.length > 0) {
                    for (var i = 0; i < arr.length; i++) {
                        if (arr[i].split("|").length > 1) {
                            var person = { name: arr[i].split("|")[0], value: arr[i].split("|")[1] };
                            array.push(person);
                        }
                        else {
                            var person = { name: arr[i], value: arr[i] };
                            array.push(person);
                        }
                    }
                }
            }
            else {
                if (arr.length > 0) {
                    for (var i = 0; i < arr.length; i++) {
                        var person = { name: arr[i], value: arr[i] };
                        array.push(person);
                    }
                }
            }
            return array;
        },
        MakeNormalArray: function (value) {
            try {
                return JSON.parse(value);
            }
            catch (ex) {
                return value;
            }
        },
        MakeSelectArray: function (objItem) {
            var array = [];
            var arr = String(objItem.select_options).split(',');
            if (arr.length > 0 && objItem.picklist_options == 'Lookup' && objItem.form_field_visibility == "YES" && objItem.dt_code == "select") {
                var person = { name: arr[0], value: arr[1] };
                array.push(person);
            }
            return array
        },
        GetValidation: function (object) {
        
            var obj = {};
            if (object.is_required) {
                obj['required'] = true;
            }
            if (object.length > 0) {
                obj['max'] = object.length;
            }
            if (object.validation_type == "email") {
                obj['email'] = true;
            }
            if (object.dt_code == "date") {
                obj['date_format'] = String(Globalize.culture().calendar.patterns.d).toUpperCase();
            }
            if (object.dt_code == "decimal") {
                obj['decimal'] = 2;
            }
            if (object.dt_code == "int" || object.dt_code == "bigint") {
                obj['numeric'] = true;
            }
            if (object.dt_code == "url") {
                obj['url'] = 'require_protocol';
            }
            return obj;
        },
        onCancel: function () {
            this.$router.push({ name: 'CRMPriceBook' });
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading=true;
            const params = `id=${vm.$route.params.id}&moduleName=CRM&subModuleCode=CRM_PRICE_BOOKS&langCode=en`;
            PriceBookDataService.getFormLayout(params)
                .then(response => {
                    vm.isLoading=false;
                    vm.LeadData = response.data.data;
                    if (vm.LeadData != null && vm.LeadData.length > 0) {
                        var groupId = this.LeadData[0].group_id;
                        $.each(vm.LeadData, $.proxy(function (index, item) {
                            groupId = item.group_id;
                            if (item.dt_code == 'date') {
                                item.value= item.value==''?null:item.value;
                            }
                            if (item.dt_code == 'checkbox') {
                                item.select_value = item.value.split(',');
                            }
                            if (item.name == "price_book_model") {
                                vm.discountType = (item.value == null || item.value == "") ? 0 : item.value;
                            }
                            var chkGroup = $.grep(vm.GroupBy, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(vm.LeadData, function (element, index) {
                                    return element.group_id == groupId;
                                });
                                if (vm.pricebookid > 0) {
                                    $.each(returnedData, function (filedIndex, fieldItem) {
                                        if (fieldItem.dt_code == "select") {
                                            try {
                                                fieldItem.value = fieldItem.value.trim();
                                            } catch (ex) { console.log("value is null while trimming") }
                                        }
                                        if (fieldItem.dropdown_type == "custom" && fieldItem.picklist_options == "Lookup") {
                                            fieldItem.select_options = vm.MakeNormalArray(fieldItem.select_options);
                                            fieldItem.select_value = fieldItem.select_options;
                                            if (fieldItem.select_options != null && fieldItem.select_options.length > 0) {
                                                fieldItem.value = fieldItem.select_options[0];
                                            }
                                        }
                                        else if (fieldItem.dt_code == "decimal" && fieldItem.form_field_visibility == "YES") {
                                            fieldItem.value = parseFloat(fieldItem.value, 10).toFixed(2);
                                        }
                                        else if (fieldItem.dt_code == "date") {
                                            fieldItem.value =vm.$options.filters.formatDate(fieldItem.value);
                                        }
                                    });
                                }
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        group_id: groupId,
                                        group_name: returnedData[0].group_name,
                                        group_display_name: returnedData[0].group_display_name,
                                        Data: returnedData
                                    };
                                    vm.GroupBy.push(objGroup);
                                }
                            }
                        }, vm));
                        if (response.data.otherData != '' && response.data.otherData !== undefined) {
                            vm.isPriceInfo = true;
                            vm.priceInformation = response.data.otherData;
                        }
                        setTimeout(function () { vm.DependentOption[0].minDate = new Date(Date.now()); }, 1000);
                    }
                    setTimeout(function(){vm.CheckBoxBootstrap()}, 500);
                    setTimeout(function(){vm.RadioButtonBootstrap()}, 500);
                    // setTimeout(function(){vm.ShowPopOver()}, 500);
                    var frmFields = {}
                    vm.LeadData.forEach((item) => {
                        if (item.astype != '' && item.form_field_visibility == 'YES') {
                            frmFields[item.name] = item.value;
                        }
                    })
                    vm.priceInformation.forEach((item,index) => {
                        frmFields['rangeFrom'+index]=item.rangeFrom;
                        frmFields['rangeTo'+index]=item.rangeTo;
                        frmFields['discount'+index]=item.discount;
                    })
                    vm.$refs.form.resetForm({ values: frmFields });
                })
        },       
  
    },
}
</script>
<template>
    
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
            <div id="divMainLeadData" v-cloak>
               <div class="row">
                    <div class="col-md-12 padding-t_8">
                        <div class="theme-primary partition-full">
                            <span class="p-name text-white">{{$t('ManagePriceBook')}}</span>
                            <span class="float-right">
                                <!-- <a href="javascript:;" class="p-action-btn text-white" title="PageTour" @click="setTourGuideSteps(0)">
                                    <em class="fa fa-road" aria-hidden="true"></em>
                                    <br />{{$t('PageTour')}}
                                </a> -->
                            </span>
                        </div>
                    </div>
                </div>


                <div class="border p-3">
                    <Form v-slot="{  valid,errors}" ref="form">
                        <DynamicForm :schema="GroupBy" :dateformat="dateformat" :DependentOption="DependentOption"  
                        v-on:setDependentDate="DependentDateObject" :subModuleCode="$route.params.subModuleCode" 
                        v-on:onchangeForNormalDDL="onchangeForNormalDDL"  v-on:onSearchDDL="onSearch"  v-on:setSelectedValue="setSelectedValue"  />
                        <div class="listing p-3" v-if="isPriceInfo">
                            <div class="table-responsive">
                                <table id="priceBookTable" class="table table-bordered dt-responsive nowrap mt-0" style="width: 100%; min-width: 650px!important;" aria-describedby="pricebooktable">
                                    <thead class="thead-bg">
                                        <tr class="text-capitalize">
                                             <th scope="col"><span>{{$t('RangeFrom')}}</span></th>
                                             <th scope="col"><span>{{$t('RangeTo')}}</span></th>
                                             <th scope="col"><span>{{$t('Discount')}} {{(discountType == 2)?'(%)': '(Flat)' }}</span></th>
                                             <th scope="col" class="text-center"><span>{{$t('Action')}}</span></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item,index) in priceInformation" :key="index">
                                            <td>
                                                <Field :name="'rangeFrom'+index" :rules="{'required':true,'numeric':true,'min_value':((index>0)?(parseInt(priceInformation[index-1].rangeTo)+1):1)}" label="Range From" v-slot="{errors,touched,invalid}">
                                                <input  type="text" v-bind:class="{ 'form-control': true, 'is-invalid': touched && invalid }" v-model="item.rangeFrom" 
                                                       v-bind:name="'rangeFrom'+index" :id="'rangeFrom'+index" v-on:input="changevalue(item)"   />
                                                       <span   :name="rangeFrom" class="invalid-feedback d-block">{{errors[0]}}</span>                                
                                               </Field>                                               
                                            </td>
                                            <td>
                                                <Field :name="'rangeTo'+index" :rules="{'required':true,'numeric':true,'min_value':(parseInt(item.rangeFrom =='' ? 0: item.rangeFrom)+1)}" label="Range To" v-slot="{errors,touched,invalid}">
                                                <input   type="text" v-bind:class="{ 'form-control': true, 'is-invalid': touched && invalid }" v-model="item.rangeTo"  v-bind:name="'rangeTo'+index" :id="'rangeTo'+index"/>
                                                <span   :name="rangeTo" class="invalid-feedback d-block">{{errors[0]}}</span>                                
                                               </Field>
                                            </td>
                                            <td>
                                                <Field :name="'discount'+index" :rules="{'required':true,'numeric':true,'max_value':100}" label="Discount" v-slot="{errors,touched,invalid}">
                                                <input   type="text" v-bind:class="{ 'form-control': true, 'is-invalid': touched && invalid }" v-model="item.discount" v-on:keyup="discountFix(index,item.discount)" 
                                                v-bind:name="'discount'+index" :id="'discount'+index" />
                                                <span   :name="discount" class="invalid-feedback d-block">{{errors[0]}}</span>                                
                                               </Field>
                                            </td>
                                            <td class="text-center single-action"><a href="javascript:;" v-on:click="deletePriceDetailRow(index)">
                                                <em class="fa fa-trash action-icon text-danger"></em></a></td>
                                        </tr>
                                    </tbody>
                                </table>
                                <div class="col-lg-12 add-form-field text-right py-3">
                                    <a href="javascript:;" class="btn btn-success" v-on:click="addNewForPriceDetails()"><em class="fa fa-plus pr-2" aria-hidden="true"></em>{{$t('AddMore')}}</a>
                                </div>
                            </div>
                        </div>
                        <div class="row flex-row-reverse mt-2">
                            <div class="col-lg-6 text-right">
                                <a href="javascript:;"
                                    
                                    v-bind:class="{disabled: submited}" v-on:click="SubmitData( valid,errors)"
                                    class="btn btn-success add_pricebbok_a" title="Save">
                                    <em class="fa fa-save pr-2"></em>{{$t('Save')}}
                                </a>&nbsp;
                                <a href="javascript:void(0)" class="btn btn-danger" title="Cancel" @click="onCancel">
                                    <em class="fa fa-close pr-2"></em>{{$t('Cancel')}}
                                </a>
                            </div>
                            <div class="col-lg-6">
                                <div class="py-2">
                                     <small class="text-danger">{{$t('MandatoryString')}}</small>
                                </div>
                            </div>
                        </div>

                    </Form>
                </div>
            </div>
        </section>
  
</template>